* {
    box-sizing: border-box;
    font-family: 'Lato', sans-serif;
  }

  body,
  html,
  #root {
    background-color: #e3e8ed;
    display: flex;
    flex-direction: column;
    flex: 1;
    margin: 0;
    padding: 0;
    color: #000;
  }
#search-container {
  width: 100%;
  transform: translateY(60%);
  padding: 5px 30px;
  border-radius: 2px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}
#suggestion-container {
  width: 100%;
  /* transform: translateY(60%); */
  margin-top: 80px;
  padding: 5px 30px;
  border-radius: 2px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}
#suggestion-container li:hover {
  color: rgb(63, 63, 156);
  cursor: pointer;
}
ul {
  list-style-type: none;
}
p span {
  font-weight: 400
}
.emailAddress {
  font-size: 0.7em;
  padding: 3px;
}
